<template>
  <div class="addArticle">
    <el-row>
      <el-col :span="8">

        <el-upload
          class="upload-demo"
          drag
          list-type="picture"
          :limit="1"
          :action="$baseURL+'/upload/uploadFileMasterGraph'"
          :headers="headers"
          multiple
          :file-list="fileList"
          :on-success="uploadSuccess"
          :on-error="uploadError"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将
            <span style="color: red;">主图</span>拖到此处，或
            点击上传
            <p style="color: #005aff;">建议图片比例统一</p>
          </div>
        </el-upload>
      </el-col>

      <el-col :span="16">
        <el-form ref="article" :model="article" label-width="80px">
          <el-form-item label="文章标题">
            <el-input v-model="article.title" placeholder="50字符内" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="文章描述">
            <el-input v-model="article.intro" placeholder="500字符内" maxlength="500"></el-input>
          </el-form-item>
          <el-form-item label="关键字">
            <el-input
              v-model="article.keyword"
              placeholder="3-5个关键字（关键字与关键字之间用英文逗号隔开）"
              maxlength="80"
              class="keyword"
            ></el-input>
            
            <el-switch
              class="hot"
              style="display: block"
              v-model="value2"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="首页展示"
            ></el-switch>

            <el-button class="subBtn" type="primary" @click="submitArticle">修改</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <Markdown :onchange="change" v-bind:initData="initData"></Markdown>
  </div>
</template>

<script>
import Markdown from "../../components/markdown/markdown-editor";
import initData from "../../markData.js";
export default {
  name: "addArticle",
  data() {
    return {
      value2: false,
      categoryList: [],
      article: {
        c_id: "", //文章类别id
        title: "",
        keyword: "",
        describe: "",
        picture: "",
        markdown: "",
        html: "",
        hot: "",
        text: ""
        // content: {}
      },
      fileList: [{ name: "原图片", url: "" }],
      headers: { token: this.$store.getters.token },
      initData: "initData"
      // initData:'# vue-xuAdmin 初始值### 多语言代码高亮 Codes↵↵#### 行内代码 In…? ↵Andrew->>China: I am good thanks!↵```↵↵### End", html: "<h1 id="h1-vue-xuadmin-"><a name="vue-xuAdmin 初始值"…header-link octicon octicon-link"></span>End</h3>", text: "vue-xuAdmin 初始值多语言代码高亮 Codes行内代码 Inline code执行命令：n…ow are you? ↵Andrew->>China: I am good thanks!End'
    };
  },
  components: { Markdown },
  created() {
    this.headers.token = this.$store.getters.token;
    this.article = this.$router.currentRoute.params;
    this.fileList[0].url = this.article.picture;
    this.initData = this.article.markdown;
    if (this.article.hot == "1") {
      this.value2 = true;
    }

  },
  methods: {
    uploadSuccess(response, file, fileList) {
      console.log(response, file, fileList);
      if (response.ok) {
        this.article.picture = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },
    uploadError(error, file, fileList) {
      console.log(error, file, fileList);
      this.$message({
        showClose: true,
        message: "上传失败！",
        type: "error"
      });
      return false;
    },
    change() {
      // console.log("编辑", arguments[0]);
      this.article.markdown = arguments[0].markdown;
      this.article.html = arguments[0].html;
      this.article.text = arguments[0].text;
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    handleSelect(item) {
      console.log(item);
    },
    submitArticle() {
      // this.initData += this.article;
      if(this.value2){
        this.article.hot="1"
      }else{
        this.article.hot="0"
      }
      this.$axios({
        method: "put",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/about/updateById",
        data: this.article,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
      console.log("源码", this.article);
    }
  }
};
</script>

<style >
.upload-demo {
  width: 80%;
  /* margin: 0 auto; */
  position: relative;
}
.el-upload-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 28px;
  left: 10px;
}
.upload {
  background: #fff;
  border: 0;
  color: #409eff;
}
.articleCategory {
  width: 20%;
  margin-left: 0px;
}
.subBtn {
  width: 10%;
  margin: 0 auto;
  float: right;
}
.keyword {
  float: left;
  width: 48%;
}
.hot{
   width: 18%;
  margin: 10px 8px;
  float: left;
}
</style>



